$cor-header: #4BAA8D;



/********* CONFIGURAÇÔES *********/
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

a {
    display: block;
    text-decoration: none;
}

ul, li {
    list-style: none;
}

img {
    display: block;
    width: 100%;
}


